body {
  background-color: #0a0a0a;
  background-image: 
    linear-gradient(rgba(0, 255, 0, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  color: #00a900;
  font-family: 'Courier New', monospace;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  color: #00a900;
}

h2 {
  color: #00a900;
  border-bottom: 1px solid #00a900;
  padding-bottom: 10px;
}

.profile {
  position: absolute;
  bottom: 60px;
  left: 20px;
  background-color: rgba(10, 10, 10, 0.7); /* Semi-transparent background */
  padding: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  width: calc(100% - 40px); /* Adjust based on your layout needs */
  max-width: 600px; /* Adjust as needed */
}

.profile-image {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  border: 2px solid #00a900;
  object-fit: cover;
  margin-right: 20px; /* Add space between image and text */
}

.profile-text {
  flex: 1;
}

.profile-text h1 {
  margin: 0 0 5px 0; /* Reduce top and bottom margins */
}

.profile-text p {
  margin: 0; /* Remove default paragraph margins */
}

.about, .contact {
  margin-bottom: 30px;
}

a {
  color: #00ffff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Terminal-like effect */
.container::before {
  content: "> ";
  color: #00a900;
  font-weight: bold;
}

/* Blinking cursor effect */
.container::after {
  content: "|";
  color: #00a900;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}

.navigation {
  /* Styles for the navigation section */
  margin-top: 20px;
  text-align: center;
}

.btn-projects {
  /* Styles for the projects button */
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.banner {
  position: relative;
  background-image: url('../public/image.jpg');
  background-size: cover;
  background-position: center;
  height: 300px; /* Adjust as needed */
  margin-bottom: 30px;
}